.swiper {
    width: 100%;
    height: 100%;
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
/*     background: rgb(0, 110, 150);
 */    display: flex;
    align-items: center;
    justify-content: space-around;

    button.prev, button.next {
      border: none;
      font-size: 40px;
      background: transparent;
      svg path {
        color: #000;
      }
    }
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: scale-down;
  }